import Vue from 'vue'
import Router from 'vue-router'
import HomePage from '@/views/Home'
import indexPage from '@/components/pages'
import InsuranceIndex from '@/views/nav1/insurance_index.vue' // 
import InsuranceProduct from '@/views/nav1/insurance_product.vue' // 
import PolicySource from '@/views/nav1/policy_source.vue' // 
import PolicyGroup from '@/views/nav1/policy_group.vue' // 
import PolicyPperson from '@/views/nav1/policy_person.vue' // 
import ClaimsIndex from '@/views/nav1/claims_index.vue' // 
import MedicalData from '@/views/nav1/medical_data.vue' // 
import ApplicationInfo from '@/views/nav1/application_info.vue' // 
import ClaimData from '@/views/nav1/claim_data.vue' // 
import QueryData from '@/views/nav1/query_data.vue' // 

// form
import Addproduct from '@/views/nav1/form/addproduct_form.vue' // 

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      component: HomePage,
      children: [
        { path: '', component: indexPage, name: '' }
      ]
    },
    {
      path: '/nav1',
      name: '',
      component: HomePage,
      children: [
        { path: 'insurance_index', component: InsuranceIndex, name: '' },
        { path: 'insurance_product', component: InsuranceProduct, name: '' },
        { path: 'policy_source', component: PolicySource, name: '' },
        { path: 'policy_group', component: PolicyGroup, name: '' },
        { path: 'policy_person', component: PolicyPperson, name: '' },
        { path: 'claims_index', component: ClaimsIndex, name: '' },
        { path: 'medical_data', component: MedicalData, name: '' },
        { path: 'application_info', component: ApplicationInfo, name: '' },
        { path: 'claim_data', component: ClaimData, name: '' },
        { path: 'query_data', component: QueryData, name: '' },

        // form
        { path: 'addproduct_form', component: Addproduct, name: '' }
      ]
    },
    { path: '*', redirect: '/' }
  ]

})



