<template>
  <section>
    <!---->
    <el-col :span="24" class="toolbar">
      <el-form :inline="true" :model="formInline" ref="formInline" class="demo-form-inline">
        <el-form-item label=":" prop="value">
          <el-select v-model="formInline.value" placeholder="">
            <el-option
              v-for="(item,index) of selectDate"
              :key="index"
              :label="item.name"
              :value="item.val">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label=":" prop="product">
          <el-input v-model="formInline.product" placeholder=""></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="getUsers"></el-button>
          <el-button @click="resetForm('formInline')"></el-button>
        </el-form-item>
      </el-form>
    </el-col>

    <!---->
    <el-col :span="24" class="table_top_bar">
      <router-link to="addproduct_form"><el-button icon="el-icon-plus"></el-button></router-link>
      <el-button icon="el-icon-date"></el-button>
      <el-button icon="el-icon-edit-outline"></el-button>
      <el-button icon="el-icon-sort">/</el-button>
    </el-col>
    <el-table
      border
      stripe
      highlight-current-row
      ref="multipleTable"
      v-loading="listLoading"
      :data="users"
      tooltip-effect="dark">
      <el-table-column type="selection" width="35"></el-table-column>
      <el-table-column prop="product" label="" show-overflow-tooltip></el-table-column>
      <el-table-column prop="company" label="" show-overflow-tooltip></el-table-column>
      <el-table-column prop="type" label="" show-overflow-tooltip></el-table-column>
      <el-table-column prop="money" label="" show-overflow-tooltip></el-table-column>
      <el-table-column prop="name" label="" show-overflow-tooltip></el-table-column>
      <el-table-column prop="minage" label="" show-overflow-tooltip></el-table-column>
      <el-table-column prop="maxage" label="" show-overflow-tooltip></el-table-column>
      <el-table-column prop="type" label="" show-overflow-tooltip></el-table-column>
      <el-table-column prop="minnum" label="" show-overflow-tooltip></el-table-column>
      <el-table-column prop="minnum" label="" show-overflow-tooltip></el-table-column>
      <el-table-column prop="minage" label="" show-overflow-tooltip></el-table-column>
      <el-table-column prop="type" label="" show-overflow-tooltip></el-table-column>
      <el-table-column prop="sex" label="" show-overflow-tooltip>
        <template slot-scope="scope">
          {{scope.row.sex === 0 ? "" : ""}}
        </template>
      </el-table-column>
      <el-table-column prop="sex" label="" show-overflow-tooltip>
        <template slot-scope="scope">
          {{scope.row.sex === 0 ? "" : ""}}
        </template>
      </el-table-column>
      <el-table-column prop="birth" label="" show-overflow-tooltip></el-table-column>
      <el-table-column prop="sex" label="" show-overflow-tooltip width="70">
        <template slot-scope="scope">
          <!--scope.row.sex-->
          <el-tag v-if="scope.row.sex" type="success"></el-tag>
          <el-tag v-else type="danger"></el-tag>
        </template>
      </el-table-column>
    </el-table>

    <!---->
    <el-col :span="24" class="table_foot_bar">
      <el-button @click="toggleSelection()"></el-button>
    </el-col>

    <!---->
    <el-col :span="24">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="currentPage"
        :page-sizes="[10, 20, 30, 40]"
        :page-size="pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </el-col>

  </section>
</template>

<script>
import { getUserListPage } from '../../api/api'
export default {
  data () {
    return {
      formInline: {
        product: '',
        value: ''
      },
      selectDate: [
        { name: '1', val: 'option1' },
        { name: '2', val: 'option2' },
        { name: '3', val: 'option3' }
      ],
      total: 0,
      page: 1,
      pagesize: 10,
      users: [], // 
      listLoading: false, // loading
      currentPage: 1 // 
    }
  },
  methods: {
    // 
    getUsers () {
      let para = {
        page: this.page,
        product: this.formInline.product,
        pagesize: this.pagesize
      }
      this.listLoading = true
      getUserListPage(para).then((res) => {
        console.log(res.data)
        this.total = res.data.total
        this.users = res.data.users
        this.listLoading = false
      })
    },
    // 
    resetForm (formName) {
      this.$refs[formName].resetFields();
      this.getUsers()
    },
    // 
    handleSizeChange (val) {
      console.log(` ${val} `)
      this.pagesize = val
      this.getUsers()
    },
    handleCurrentChange (val) {
      console.log(`: ${val}`)
      this.page = val
      this.getUsers()
    },
    // 
    toggleSelection (rows) {
      if (rows) {
        rows.forEach(row => {
          this.$refs.multipleTable.toggleRowSelection(row)
        })
      } else {
        this.$refs.multipleTable.clearSelection()
      }
    }
  },
  mounted () {
    this.getUsers()
  }
}
</script>



