<template>
  <section>
    <!---->
    <el-col :span="24" class="toolbar">
      <el-form :inline="true" :model="formInline" ref="formInline" class="demo-form-inline">
        <el-form-item label=":" prop="value">
          <el-select v-model="formInline.value" placeholder="">
            <el-option v-for="(item,index) of selectDate" :key="index" :label="item.name" :value="item.val">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label=":" prop="value">
          <el-select v-model="formInline.value" placeholder="">
            <el-option v-for="(item,index) of selectDate" :key="index" :label="item.name" :value="item.val">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label=":" prop="value">
          <el-select v-model="formInline.value" placeholder="">
            <el-option v-for="(item,index) of selectDate" :key="index" :label="item.name" :value="item.val">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitForm('formInline')"></el-button>
          <el-button @click="resetForm('formInline')"></el-button>
        </el-form-item>
      </el-form>
    </el-col>

    <!---->
    <el-col :span="24" class="table_top_bar">
      <el-button icon="el-icon-plus" @click="dialogVisible = true"></el-button>
      <el-button icon="el-icon-refresh"></el-button>
    </el-col>
    <el-table border stripe highlight-current-row ref="multipleTable" v-loading="listLoading" :data="users" tooltip-effect="dark">
      <el-table-column type="selection" width="35"></el-table-column>
      <el-table-column prop="product" label="" show-overflow-tooltip></el-table-column>
      <el-table-column prop="company" label="" show-overflow-tooltip></el-table-column>
      <el-table-column prop="company" label="" show-overflow-tooltip></el-table-column>
      <el-table-column prop="company" label="" show-overflow-tooltip></el-table-column>
      <el-table-column prop="sex" label="" show-overflow-tooltip>
        <template slot-scope="scope">
          <el-tag v-if="scope.row.sex" type="success"></el-tag>
          <el-tag v-else type="danger"></el-tag>
        </template>
      </el-table-column>
      <el-table-column prop="birth" label="" show-overflow-tooltip></el-table-column>
    </el-table>

    <!---->
    <el-col :span="24" class="table_foot_bar">
      <el-button @click="toggleSelection()"></el-button>
    </el-col>

    <!---->
    <el-col :span="24">
      <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage" :page-sizes="[10, 20, 30, 40]"
        :page-size="pagesize" layout="total, sizes, prev, pager, next, jumper" :total="total">
      </el-pagination>
    </el-col>

    <!--  -->
    <template>
      <el-dialog :visible.sync="dialogVisible" width="40%">
        <span slot="title"></span>
        <div class="dialogCont">
          <el-form label-width="80px">
          <el-row>
            <el-col :span="12">
                <div class="grid-content bg-purple">
                    <el-form-item label=":">
                      <el-select v-model="formInline.value" placeholder="">
                        <el-option v-for="(item,index) of selectDate" :key="index" :label="item.name" :value="item.val">
                        </el-option>
                      </el-select>
                    </el-form-item>

                    <el-form-item label=":">
                      <el-select v-model="formInline.value" placeholder="">
                        <el-option v-for="(item,index) of selectDate" :key="index" :label="item.name" :value="item.val">
                        </el-option>
                      </el-select>
                    </el-form-item>

                    <el-form-item label=":">
                      <el-select v-model="formInline.value" placeholder="">
                        <el-option v-for="(item,index) of selectDate" :key="index" :label="item.name" :value="item.val">
                        </el-option>
                      </el-select>
                    </el-form-item>
                    <el-form-item label=":">
                      <el-upload
                        class="upload-demo"
                        action="https://jsonplaceholder.typicode.com/posts/"
                        :on-preview="handlePreview"
                        :on-remove="handleRemove"
                        :before-remove="beforeRemove"
                        :limit="1"
                        :on-exceed="handleExceed">
                        <el-button size="small" type="primary"></el-button>
                        <!-- <div slot="tip" class="el-upload__tip">jpg/png500kb</div> -->
                      </el-upload>
                    </el-form-item>
                    
                </div>
              </el-col>
            <el-col :span="12">
              <div class="grid-content bg-purple-light">
                <el-form-item label=":">
                  <el-button size="small" type="primary"></el-button>
                </el-form-item>
              </div>
              </el-col>
          </el-row>
          </el-form>
        </div>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false"> </el-button>
          <el-button type="primary" @click="dialogVisible = false"> </el-button>
        </span>
      </el-dialog>
    </template>
  </section>
</template>

<script>
  
  import { getUserListPage } from "../../api/api";
  
  export default {
    data() {
      return {
        dialogVisible: false,
        formInline: {
          name: "",
          value: ""
        },
        selectDate: [{
            name: "1",
            val: "option1"
          },
          {
            name: "2",
            val: "option2"
          },
          {
            name: "3",
            val: "option3"
          }
        ],
        total: 0,
        page: 1,
        pagesize: 10,
        users: [], // 
        listLoading: false, // loading
        currentPage: 1 // 
      };
    },
    methods: {
      open() {
          this.show = true;
      },
      // 
      getUsers() {
        let para = {
          page: this.page,
          name: this.formInline.name,
          pagesize: this.pagesize
        };
        this.listLoading = true;
        getUserListPage(para).then(res => {
          console.log(res.data);
          this.total = res.data.total;
          this.users = res.data.users;
          this.listLoading = false;
        });
      },
      // 
      resetForm(formName) {
        this.$refs[formName].resetFields();
      },
      // 
      submitForm() {
        console.log(this.formInline.name, this.formInline.value);
      },
      // 
      handleSizeChange(val) {
        console.log(` ${val} `);
        this.pagesize = val;
        this.getUsers();
      },
      handleCurrentChange(val) {
        console.log(`: ${val}`);
        this.page = val;
        this.getUsers();
      },
      // 
      toggleSelection(rows) {
        if (rows) {
          rows.forEach(row => {
            this.$refs.multipleTable.toggleRowSelection(row);
          });
        } else {
          this.$refs.multipleTable.clearSelection();
        }
      },
      // 
      handleRemove(file) {
        console.log(file);
      },
      handlePreview(file) {
        console.log(file);
      },
      handleExceed(files) {
        this.$message.warning(` 1  ${files.length}  ${files.length} `);
      },
      beforeRemove(file) {
        return this.$confirm(` ${ file.name }`);
      }
    },
    mounted() {
      this.getUsers();
    }
  };

</script>



