<template>
  <section>
    <!---->
    <el-col :span="24" class="toolbar">
      <el-form :inline="true" :model="formInline" :rules="rules" ref="formInline" class="demo-form-inline">
        <el-form-item label="" prop="name">
          <el-input v-model="formInline.name" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="age">
          <el-input v-model.number="formInline.age" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="region">
          <el-select v-model="formInline.region" placeholder="">
            <el-option label="" value="shanghai"></el-option>
            <el-option label="" value="beijing"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-col :span="11">
            <el-form-item prop="start_date">
              <el-date-picker type="date" placeholder="" v-model="formInline.start_date" :picker-options="pickerBeginDateBefore"></el-date-picker>
            </el-form-item>
          </el-col>
          <el-col class="form-time-line" :span="2"></el-col>
          <el-col :span="11">
            <el-form-item prop="end_date">
              <el-date-picker type="date" placeholder="" v-model="formInline.end_date" :picker-options="pickerBeginDateAfter"></el-date-picker>
            </el-form-item>
          </el-col>
        </el-form-item>
        <el-form-item label="">
          <el-date-picker
            v-model="formInline.daterange"
            type="daterange"
            range-separator=""
            start-placeholder=""
            end-placeholder="">
          </el-date-picker>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitForm('formInline')"></el-button>
          <el-button @click="resetForm('formInline')"></el-button>
        </el-form-item>
      </el-form>
    </el-col>

    <!---->
    <el-col :span="24" class="table_top_bar">
      <el-button type="primary" icon="el-icon-upload"></el-button>
      <el-button type="primary" icon="el-icon-search"></el-button>
      <el-button type="danger" icon="el-icon-check"></el-button>
    </el-col>
    <el-table
      border
      highlight-current-row
      ref="multipleTable"
      v-loading="listLoading"
      :data="users"
      tooltip-effect="dark">
      <el-table-column type="selection" width="55"></el-table-column>
      <el-table-column prop="img" label="">
        <template slot-scope="scope">
          <img :src="scope.row.img">
        </template>
      </el-table-column>
      <el-table-column prop="id" label="ID" show-overflow-tooltip></el-table-column>
      <el-table-column prop="name" label="" show-overflow-tooltip></el-table-column>
      <el-table-column prop="sex" label="" show-overflow-tooltip>
        <template slot-scope="scope">
          {{scope.row.sex === 0 ? "" : ""}}
        </template>
      </el-table-column>
      <el-table-column prop="age" label="" show-overflow-tooltip></el-table-column>
      <el-table-column prop="birth" label="" show-overflow-tooltip></el-table-column>
      <el-table-column prop="product" label="" show-overflow-tooltip></el-table-column>
      <el-table-column prop="addr" label="" show-overflow-tooltip></el-table-column>
    </el-table>

    <!---->
    <el-col :span="24" class="table_foot_bar">
      <el-button @click="toggleSelection([users[1], users[2]])"></el-button>
      <el-button @click="toggleSelection()"></el-button>
    </el-col>

    <!---->
    <el-col :span="24">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="currentPage"
        :page-sizes="[10, 20, 30, 40]"
        :page-size="pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </el-col>

  </section>
</template>
<script>
import { getUserListPage } from '../../api/api'
export default {
  data () {
    return {
      formInline: {
        age: '',
        name: '',
        region: '',
        start_date: '',
        end_date: '',
        daterange: ''
      },
      total: 0,
      page: 1,
      pagesize: 10,
      users: [], // 
      listLoading: false, // loading
      isloading: false, // loading
      editLoading: false, // loading
      editFormRules: {
        name: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      currentPage: 1, // 
      pickerBeginDateBefore: { // 
        disabledDate: (time) => {
          let beginDateVal = this.formInline.end_date
          if (beginDateVal) {
            return time.getTime() > beginDateVal
          }
        }
      },
      pickerBeginDateAfter: { // 
        disabledDate: (time) => {
          let beginDateVal = this.formInline.start_date
          if (beginDateVal) {
            return time.getTime() < beginDateVal
          }
        }
      },
      rules: {
        age: [
          {required: true, message: ''},
          {type: 'number', message: ''}
        ],
        name: [
          {required: true, message: ''}, // , trigger: 'blur'
          {min: 3, max: 5, message: ' 3  5 '}
        ],
        region: [
          {required: true, message: ''} // , trigger: 'change'
        ],
        start_date: [
          {type: 'date', required: true, message: ''}
        ],
        end_date: [
          {type: 'date', required: true, message: ''}
        ]
      }
    }
  },
  methods: {
    // 
    getUsers () {
      let para = {
        page: this.page,
        name: this.formInline.name,
        pagesize: this.pagesize
      }
      this.listLoading = true
      getUserListPage(para).then((res) => {
        console.log(res.data)
        this.total = res.data.total
        this.users = res.data.users
        this.listLoading = false
      })
    },
    // 
    submitForm (formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          console.log('submit!!')
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    // 
    resetForm (formName) {
      this.$refs[formName].resetFields()
    },
    // 
    handleSizeChange (val) {
      console.log(` ${val} `)
      this.pagesize = val
      this.getUsers()
    },
    handleCurrentChange (val) {
      console.log(`: ${val}`)
      this.page = val
      this.getUsers()
    },
    // 
    toggleSelection (rows) {
      if (rows) {
        rows.forEach(row => {
          this.$refs.multipleTable.toggleRowSelection(row)
        })
      } else {
        this.$refs.multipleTable.clearSelection()
      }
    }
    // // 
    // handleEdit: function (index, row) {
    //   this.editFormVisible = true
    //   this.editForm = Object.assign({}, row)
    // }
  },
  mounted () {
    this.getUsers()
  }
}
</script>



