<template>
  <section>
    <!---->
    <el-col :span="24" class="toolbar">
      <el-form :inline="true" :model="formInline" ref="formInline" class="demo-form-inline">
        <el-form-item label=":" prop="value">
          <el-input v-model="formInline.name" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label=":" prop="value">
          <el-select v-model="formInline.value" placeholder="">
            <el-option v-for="(item,index) of selectDate" :key="index" :label="item.name" :value="item.val">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitForm('formInline')"></el-button>
          <el-button @click="resetForm('formInline')"></el-button>
        </el-form-item>
      </el-form>
    </el-col>

    <!--  -->
    <el-col :span="24" class="table_top_bar">
      <el-button icon="el-icon-circle-check-outline"></el-button>
      <el-button icon="el-icon-circle-close-outline"></el-button>
      <el-button icon="el-icon-edit-outline"></el-button>
      <el-button icon="el-icon-search"></el-button>
    </el-col>
    <!---->
    <el-table
    border
    stripe
    highlight-current-row
    ref="multipleTable"
    element-loading-text=""
    element-loading-spinner="el-icon-loading"
    element-loading-background="rgba(0, 0, 0, 0.8)"
    v-loading="listLoading"
    :data="users"
    tooltip-effect="dark">
      <el-table-column type="selection" width="35"></el-table-column>
      <el-table-column prop="product" label="" show-overflow-tooltip></el-table-column>
      <el-table-column prop="company" label="" show-overflow-tooltip></el-table-column>
      <el-table-column prop="birth" label="" show-overflow-tooltip></el-table-column>
      <el-table-column prop="minnum" label="" show-overflow-tooltip>
        <template slot-scope="scope">
          <!--scope.row.sex-->
          <el-tag v-if="scope.row.minnum == '1'"></el-tag>
          <el-tag v-if="scope.row.minnum == '2'"></el-tag>
          <el-tag v-if="scope.row.minnum == '3'" type="success"></el-tag>
          <el-tag v-if="scope.row.minnum == '4'" type="info"></el-tag>
          <el-tag v-if="scope.row.minnum == '5'" type="danger"></el-tag>
        </template>
      </el-table-column>
      <el-table-column prop="minnum" label="" show-overflow-tooltip></el-table-column>
      <el-table-column prop="minnum" label="" show-overflow-tooltip></el-table-column>
      <el-table-column prop="minnum" label="" show-overflow-tooltip></el-table-column>

    </el-table>

    <!---->
    <el-col :span="24" class="table_foot_bar">
      <el-button @click="toggleSelection()"></el-button>
    </el-col>

    <!---->
    <el-col :span="24">
      <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage" :page-sizes="[10, 20, 30, 40]"
        :page-size="pagesize" layout="total, sizes, prev, pager, next, jumper" :total="total">
      </el-pagination>
    </el-col>

    
  </section>
</template>

<script>
  
  import { getUserListPage } from "../../api/api";
  
  export default {
    data() {
      return {
        dialogVisible: false,
        formInline: {
          start_date: '',
          end_date: '',
          name: "",
          value: ""
        },
        selectDate: [{
            name: "",
            val: "option1"
          },
          {
            name: "",
            val: "option2"
          },
          {
            name: "",
            val: "option3"
          },
          {
            name: "",
            val: "option4"
          },
          {
            name: "",
            val: "option5"
          }
        ],
        total: 0,
        page: 1,
        pagesize: 10,
        users: [], // 
        listLoading: false, // loading
        currentPage: 1, // 
        pickerBeginDateBefore: { // 
          disabledDate: (time) => {
            let beginDateVal = this.formInline.end_date
            if (beginDateVal) {
              return time.getTime() > beginDateVal
            }
          }
        },
        pickerBeginDateAfter: { // 
          disabledDate: (time) => {
            let beginDateVal = this.formInline.start_date
            if (beginDateVal) {
              return time.getTime() < beginDateVal
            }
          }
        },
      };
    },
    methods: {
      // 
      filterHandler(value, row, column) {
        console.log(value, row, column)
        // const property = column['property'];
        // return row[property] === value;
      },
      // 
      getUsers() {
        let para = {
          page: this.page,
          name: this.formInline.name,
          pagesize: this.pagesize
        };
        this.listLoading = true;
        getUserListPage(para).then(res => {
          console.log(res.data);
          this.total = res.data.total;
          this.users = res.data.users;
          this.listLoading = false;
        });
      },
      // 
      resetForm(formName) {
        this.$refs[formName].resetFields();
      },
      // 
      submitForm() {
        console.log(this.formInline.name, this.formInline.value);
      },
      // 
      handleSizeChange(val) {
        console.log(` ${val} `);
        this.pagesize = val;
        this.getUsers();
      },
      handleCurrentChange(val) {
        console.log(`: ${val}`);
        this.page = val;
        this.getUsers();
      },
      // 
      toggleSelection(rows) {
        if (rows) {
          rows.forEach(row => {
            this.$refs.multipleTable.toggleRowSelection(row);
          });
        } else {
          this.$refs.multipleTable.clearSelection();
        }
      }
    },
    mounted() {
      this.getUsers();
    }
  };

</script>


