<template>
    <section>
        <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
        <el-form :inline="true" :model="formInline" :rules="rules" ref="formInline" class="demo-form-inline" label-width="120px">
            <el-col :span="24" class="toolbar">
                <el-col :span="8" class="toolbar">
                    <el-form-item label=":" prop="name">
                        <el-input placeholder="" v-model="formInline.name"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="8" class="toolbar">
                    <el-form-item label=":" prop="region">
                        <el-select v-model="formInline.region" placeholder="">
                            <el-option label="" value="shanghai"></el-option>
                            <el-option label="" value="beijing"></el-option>
                        </el-select>
                    </el-form-item>
                </el-col>

                <el-col :span="8" class="toolbar">
                    <el-form-item label=":">
                        <el-select v-model="formInline.region" placeholder="">
                            <el-option label="" value="shanghai"></el-option>
                            <el-option label="" value="beijing"></el-option>
                        </el-select>
                    </el-form-item>
                </el-col>

                <el-col :span="8" class="toolbar">
                    <el-form-item label="ID:">
                        <el-input placeholder="ID"></el-input>
                    </el-form-item>
                </el-col>

                <el-col :span="8" class="toolbar">
                    <el-form-item label="ID:">
                        <el-input placeholder="ID"></el-input>
                    </el-form-item>
                </el-col>

                <el-col :span="8" class="toolbar">
                    <el-form-item label="ID:">
                        <el-input placeholder="ID"></el-input>
                    </el-form-item>
                </el-col>

                <el-col :span="8" class="toolbar">
                    <el-form-item label="ID:">
                        <el-input placeholder="ID"></el-input>
                    </el-form-item>
                </el-col>

                <el-col :span="8" class="toolbar">
                    <el-form-item label="ID:">
                        <el-input placeholder="ID"></el-input>
                    </el-form-item>
                </el-col>

                <el-col :span="8" class="toolbar">
                    <el-form-item label=":">
                        <el-input placeholder=""></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="8" class="toolbar">
                    <el-form-item label=":">
                        <el-col :span="10">
                            <el-input placeholder="" v-bind:disabled="radicodisabled"></el-input>
                        </el-col>
                        <el-col :span="13" :offset="1">
                            <el-radio-group v-model="formInline.radio" @change="radioChange">
                                <el-radio class="radio" :label="false"></el-radio>
                                <el-radio class="radio" :label="true"></el-radio> 
                            </el-radio-group>
                        </el-col>
                    </el-form-item>
                </el-col>

            </el-col>
            <el-col :span="24" style="text-align: center">
                <el-form-item>
                    <el-button type="primary"></el-button>
                    <el-button></el-button>
                </el-form-item>
            </el-col>
        </el-form>
    </section>
</template>
<script>
export default {
    data() {
        return {
            radicodisabled: false,
            formInline: {
                user: '',
                radio: false,
                region: ''
            },
            rules: {
                name: [
                    { required: true, message: '', trigger: 'blur' },
                    { min: 3, max: 5, message: ' 3  5 ', trigger: 'blur' }
                ],
                region: [
                    { required: true, message: '', trigger: 'change' }
                ]
            }
        }
    },
    methods: {
        radioChange(value){
            // console.log(value)
            this.radicodisabled = value
            
        }
    }
}
</script>


